<template>
  <div id="app" class="page municipio">
    <Loading :hide="hide"></Loading>
    <div class="container">
      <div class="page-content">
        <section class="showcase" v-if="emenda">
          <h2>{{emenda.objeto}}</h2>
          <div class="graficos">
          </div>
          <div class="dadosEmenda">
            
          </div>
        </section>
        <section v-else>
          nada :(
        </section>
      </div>
    </div>
  </div>
</template>

<script>
  import Loading from '@/components/Loading.vue'

  export default {
    name: 'emenda',
    components: {
      Loading
    },
    props: {
      id: String,
    },
    data: function () {
      return {
        emenda: false,
        hide: '',
      }
    }, methods: {
      getEmenda: function(id) {
        this.$http.get( this.$apiUrls.getEmendaPorId(id) ).then((response) => {
          this.emenda = response.data
          this.hide = 'hide'
        }, (error) => {
          this.hide = 'hide'
          console.log("Nao foi possivel receber emenda ")
          console.log(error)
          return false
        });
        return true;
      },
      currencyFormat(value) {
        let val = (value/1).toFixed(2).replace('.', ',')
        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
      } 
    },
    mounted: function() {
      this.getEmenda(this.id)
    }
  }
</script>

<style scoped>
  .pull-right {
    float: right;
    text-align: right;  
  }
</style>
